<template>
	<div class="condition-wrap">
		<el-menu default-active="3-1" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose" :default-openeds="openeds">
			<el-submenu index="1">
				<template slot="title">
					<span></span>
				</template>
				<el-menu-item-group>
					<!--<el-menu-item index="2-1" @click='getVal' data-id='4'><i class="el-icon-edit-outline"></i></el-menu-item>-->
					<el-menu-item index="1-1" @click='getVal' data-id='1'><i class="el-icon-menu"></i></el-menu-item>
					<el-menu-item index="1-2" @click='getVal' data-id='2'><i class="el-icon-document"></i></el-menu-item>
				</el-menu-item-group>
			</el-submenu>

			<el-submenu index="2">
				<template slot="title">
					<span></span>
				</template>
				<el-menu-item-group>
					<el-menu-item index="2-1" @click='getVal' data-id='3'><i class="el-icon-upload2"></i></el-menu-item>
					<el-menu-item index="2-2" @click='getVal' data-id='4'><i class="el-icon-sold-out"></i></el-menu-item>
				</el-menu-item-group>
			</el-submenu>
			
			<el-submenu index="3">
				<template slot="title">
					<span></span>
				</template>
				<el-menu-item-group>
					<el-menu-item index="3-1" @click='getVal' data-id='5'><i class="el-icon-message"></i></el-menu-item>
					<el-menu-item index="3-2" @click='getVal' data-id='6'><i :class="[ 'my-icon-size', moneyPage?'el-money-on':'el-money']"></i></el-menu-item>
					<el-menu-item index="3-3" @click='getVal' data-id='7'><i class="el-icon-setting"></i></el-menu-item>
				</el-menu-item-group>
			</el-submenu>

			
		</el-menu>
	</div>
</template>

<script>
	export default {
		data: function() {
			return {
				openeds: ['3-1'],
				activeId: '5',
				moneyPage: false
			}
		},
		created: function() {
			//
			let that = this;

		},
		methods: {
			handleOpen(key, keyPath) {
				//				console.log(key, keyPath);
			},
			handleClose(key, keyPath) {
				//				console.log(key, keyPath);
			},
			getVal(e) {
				let that = this;
				that.activeId = e.$el.getAttribute("data-id");
				that.activeId == 2 ? that.moneyPage = true : that.moneyPage = false;
				//user
				that.$emit('activeId',that.activeId);
			}
		}
	}
</script>

<style>
	@import url("../../assets/css/user.css");
</style>


