<template>
	<div class="inform">
		<div v-if="pageStatus == 1">
			<div class="myMoney">
				<h4> </h4>
				<el-button type="primary" class="release-gao" @click="toAddGao"> <i class="el-icon-edit el-icon--right"></i></el-button>
				<p class="border-p"></p>
			<!--</div>
			<div class="moneyList">-->
				<!--<h4> </h4>-->
				<el-row class="list-status">
					<el-col :span="6">
						<span @click="changeStatus(1)" :class=" status==1?'active':'' "></span>
					</el-col>
					<el-col :span="6">
						<span @click="changeStatus(2)" :class=" status==2?'active':'' "></span>
					</el-col>
					<el-col :span="6">
						<span @click="changeStatus(3)" :class=" status==3?'active':'' "></span>
					</el-col>
					<el-col :span="6">
						<span @click="changeStatus(4)" :class=" status==4?'active':'' "></span>
					</el-col>
				</el-row>
				<el-table 
					:data="tableData.slice((curPage-1)*pagesize,curPage*pagesize)"
					 style="width: 100%">
					 <el-table-column type="expand">
					<template slot-scope="props">
						<el-form label-position="left" inline class="table-expand">
						<el-form-item label=":">
							<span>{{ props.row.name }}</span>
						</el-form-item>
						<el-form-item label=":">
							<span>{{ props.row.shop }}</span>
						</el-form-item>
						<el-form-item label=":">
							<span>{{ props.row.id }}</span>
						</el-form-item>
						<el-form-item label="360:">
							<span>{{ props.row.shopId }}</span>
						</el-form-item>
						<el-form-item label=":">
							<span>{{ props.row.category }}</span>
						</el-form-item>
						<el-form-item label=":">
							<span>{{ props.row.address }}</span>
						</el-form-item>
						<el-form-item label=":">
							<span>{{ props.row.desc }}</span>
						</el-form-item>
						<el-form-item label=":">
							<span>{{ props.row.desc }}</span>
						</el-form-item>
						<el-form-item label=":">
							<span>{{ props.row.desc }}</span>
						</el-form-item>
						<el-form-item label=":">
							<span>{{ props.row.desc }}</span>
						</el-form-item>
						<el-form-item label=":">
							<span>{{ props.row.desc }}</span>
						</el-form-item>
						<el-form-item label=":">
							<span>{{ props.row.desc }}</span>
						</el-form-item>
						</el-form>
					</template>
					</el-table-column>
					<el-table-column type="index" width="180" label="" align="center"></el-table-column>
					
					<el-table-column label="" width="180">
						<template slot-scope="scope">
							<span>{{ scope.row.title }}</span>
						</template>
					</el-table-column>
					<el-table-column label="">
						<template slot-scope="scope" width="180">
							<span>{{ scope.row.words_count }}</span>
						</template>
					</el-table-column>
					<el-table-column label="" width="180">
						<template slot-scope="scope">
							<span></span>
						</template>
					</el-table-column>
					<el-table-column label="">
						<template slot-scope="scope">
							<el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
						</template>
					</el-table-column>
				</el-table>
				<div class="pages">
					<el-pagination  
						@current-change="currentChange"
						:current-page="curPage"
						layout="prev, pager, next"
						:total="totalNum" 
						>
					</el-pagination>
				</div>
			
			</div>
		</div>
		
		<div v-if="pageStatus == 2">
			<div class="myMoney">
				<h4>  <i class="el-icon-edit-outline"></i></h4>
			</div>
			<el-row>
				<el-col :span="3">
					<label for=""><span class="input-must">*</span></span></label>
				</el-col>
				<el-col :span="10">
					<el-input v-model="form.title" class="add-title" placeholder=""></el-input>
				</el-col>
			</el-row>
			<div>
				<vue-ueditor-wrap v-model="form.msg" :config="myConfig"></vue-ueditor-wrap>
			</div>
			<div style="position: absolute;left: 100%;top: 170px;">
				<el-row>
					<el-button type="success" plain icon="el-icon-check" @click="toSave(2)"></el-button>
				</el-row>
				<el-row>
					<el-button type="success" plain icon="el-icon-check" @click="toSave(1)"></el-button>
				</el-row>
				<el-row>
					<el-button type="primary" plain icon="el-icon-upload2" @click="toupload()"></el-button>
				</el-row>
				<el-row>
					<el-button type="danger" plain icon="el-icon-close" @click="quit"></el-button>
				</el-row>
			</div>
		</div>
		
		<div v-if="pageStatus == 3">
			<div class="myMoney">
				<h4>{{form.title}}  <i class="el-icon-edit-outline"></i></h4>
			</div>
			<el-row>
			  	<el-col :span="4">
			  		<span></span>
			  	</el-col>
			  	<el-col :span="20">
			  		<el-checkbox-group v-model="quareForm.type">
					    <el-checkbox v-for="type in quareForm.typeBox" :label="type" :key="type"  @change="checkBoxs">{{type}}</el-checkbox>
					</el-checkbox-group>
			  	</el-col>
			</el-row>
		
		</div>
	</div>
</template>

<script>
	import VueUeditorWrap from 'vue-ueditor-wrap'
	import {baseUrl} from '@/api/index.js' //
	export default {
		components: {
		    VueUeditorWrap
		},
		data() {
			return {
				pageStatus:3, //1 2,3
				status:1,  //lab
				getList_url: baseUrl+'article/mylist',
				saveWork:baseUrl+'article/add',
				curPage:1, //
				pagesize:10, //10
				totalNum:0,  //
				tableData: [{
					id: '12987122',
					name: '',
					category: '',
					desc: '',
					address: '',
					shop: '',
					shopId: '10333'
					}, {
					id: '12987123',
					name: '',
					category: '',
					desc: '',
					address: '',
					shop: '',
					shopId: '10333'
					}, {
					id: '12987125',
					name: '',
					category: '',
					desc: '',
					address: '',
					shop: '',
					shopId: '10333'
					}, {
					id: '12987126',
					name: '',
					category: '',
					desc: '',
					address: '',
					shop: '',
					shopId: '10333'
					}],
				form:{
					msg:'<h2>Hello World!</h2>',
					title:'123'
				},
				quareForm:{
					typeBox:['', '', '', ''],
					type: ['' ],
				},
				myConfig: {
		            // ,
//		            serverUrl: baseUrl+'ueditor/server?action=config&noCache=1542597685533',
		            serverUrl: baseUrl+'ueditor/server',
		            // UEditor,index.html
		            UEDITOR_HOME_URL: './static/UEditor/',
		            // 
		            autoHeightEnabled: false,
		            // 
		            initialFrameHeight: 540,
		            // 
		            initialFrameWidth: '100%',
		            // 
		            enableAutoSave: false
		       }
			}
		},
		created:function(){
        	this.getList();
   		},
		methods: {
			getList:function(){
				let that = this;
				
				that.$fetch(that.getList_url,{
						page:that.curPage,
                 		pagesize:that.pagesize
				}).then((response) => {
			        console.log(response);
			        if(response.data.list.length>0){
	            		for(let i=0;i<response.data.list.length;i++){
	            			response.data.list[i].created_at = response.data.list[i].created_at.substring(0,10);
	            		}
	            		that.tableData = response.data.list;
	            		that.totalNum = response.data.count;
	            	}
			    })
			},
			currentChange: function(curPage){
                this.curPage = curPage;
                console.log(this.curPage)  //
        	},
			handleEdit(index, row) {
				console.log(index, row);
			},
			handleDelete(index, row) {
				console.log(index, row);
			},
			changeStatus:function(index){
				let that = this;
				that.status = index;
			},
			//
			toAddGao:function(){
				this.pageStatus = 2;
			},
			toSave:function(index){
				if(index==1){
					// 1 2 : 3
					let that = this;
					if(that.form.title){
						that.axios.post(that.saveWork, {
						    content: that.form.msg,
						    title: that.form.title,
						    type:2
						  })
						  .then(function (response) {
						    let res  = response.data;
						    if(res.code==0){
						    	that.$message({
						          message: '',
						          type: 'success'
						        });
						    }else{
						    	that.$message.error('');
						    }
						  })
						  .catch(function (error) {
						    console.log(error);
						  });
					}else{
						that.$message.error('');
						return;
					}
					
				}else{
					//
					let that = this;
					if(that.form.title){
						that.axios.post(that.saveWork, {
						    content: that.form.msg,
						    title: that.form.title,
						    type:2
						  })
						  .then(function (response) {
						  	let res  = response.data;
						    if(res.code==0){
						    	that.$message({
						          message: '',
						          type: 'success'
						        });
						    }else{
						    	that.$message.error('');
						    }
						    that.quit();
						  })
						  .catch(function (error) {
						    console.log(error);
						  });
					}else{
						that.$message.error('');
						return;
					}
				}
			},
			//
			toupload:function(){
				
				let that = this;
				//xxxxx
				if(that.form.title){
					that.pageStatus = 3;
				}else{
					that.$message.error('');
					return;
				}
				
				
			},
			//
			checkBoxs: function() {
//				<el-checkbox-group v-model="quareForm.type">
//					    <el-checkbox v-for="type in quareForm.typeBox" :label="type" :key="type"  @change="checkBoxs">{{type}}</el-checkbox>
//					</el-checkbox-group>
				let that = this;
				if(that.checkboxGroup.length > 1) {
					that.checkBoxPre = [];
					that.checkBoxPre.push(that.checkboxGroup[1]);
					
					that.checkboxGroup = that.checkBoxPre;
				} else {
					that.checkBoxPre = that.checkboxGroup;
				}
			},
			quit:function(){
				this.pageStatus = 1;
			}
		}
	}
</script>

<style scoped="scoped">
	@import url("../../assets/css/user");
	/*.market{
		width:95%;
	}*/
	.list-status{
		margin-bottom: 20px;
	}
	.list-status span{
		display: inline-block;
		width:50%;
		cursor: pointer;
		text-align: center;
	}
	.list-status .active{
		padding-bottom: 5px;
		border-bottom:solid 2px #409EFF;
	}
	.cash {
		font-size: 20px;
		color: #56BCEB;
	}
	
	.myMoney-cash {
		line-height: 60px;
		background-color: #eef1f6;
		padding-left: 10px;
	}
	
	.myMoney {
		margin-bottom: 50px;
	}
	.inform h4{
		display: inline-block;
		border: none;
		position: relative;
    	top: 15px;
	}
	.border-p{
		border-bottom: 1px solid #ddd;
		margin-bottom: 20px;
	}
	.table-expand {
    font-size: 0;
	}
	.table-expand label {
		width: 90px;
		color: #99a9bf;
	}
	.table-expand .el-form-item {
		margin-right: 0;
		margin-bottom: 0;
		width: 50%;
	}
</style>


