<template>
	<div class="inform">

		<div v-if="settingShow==1">
			<h4> </h4>
			<el-row>
				<!---->
				<el-col :span="24" class="col-head">
					<!--<div @click="toChangeHead" class="head-image">-->
					<div class="head-image">
						<img :src=headImg />
						<!--<p></p>-->
					</div>
				</el-col>
			</el-row>
			<!---->
			<div class="info-content">
				<!---->
				<el-row>
					<el-col :span="6"><span></span></el-col>
					<el-col :span="12" v-if="name"><span>{{name}}</span><i class="el-icon-circle-check"></i></el-col>
					<el-col :span="12" v-else><span></span></el-col>

					<el-col :span="6" v-if="name"><span class="col-change" @click="toChangeName(1)"></span></el-col>
					<el-col :span="6" v-else><span class="col-change" @click="toChangeName(2)"></span></el-col>
				</el-row>

				<!---->
				<el-row>
					<el-col :span="6"><span></span></el-col>
					<el-col :span="12" v-if="identificationStatus"><span></span><i class="el-icon-circle-check"></i></el-col>
					<el-col :span="12" v-else><span></span></el-col>

					<el-col :span="6" v-if="identificationStatus"><span class="col-change" @click="quitIdentification(1)"></span></el-col>
					<el-col :span="6" v-else><span class="col-change" @click="quitIdentification(2)"></span></el-col>
				</el-row>
				<!---->
				<!--<el-row>
					<el-col :span="6"><span></span></el-col>
					<el-col :span="12" v-if="iphone"><span>{{iphone}}</span><i class="el-icon-circle-check"></i></el-col>
					<el-col :span="12" v-else><span></span></el-col>

					<el-col :span="6" v-if="iphone"><span class="col-change" @click="toChangePhone(1)"></span></el-col>
					<el-col :span="6" v-else><span class="col-change" @click="toChangePhone(2)"></span></el-col>
				</el-row>-->
				<!---->
				<!--<el-row>
					<el-col :span="6"><span></span></el-col>
					<el-col :span="12" v-if="weixin"><span></span><i class="el-icon-circle-check"></i></el-col>
					<el-col :span="12" v-else><span></span></el-col>

					<el-col :span="6" v-if="weixin"><span class="col-change" @click="toChangeWeixin(1)"></span></el-col>
					<el-col :span="6" v-else><span class="col-change" @click="toChangeWeixin(2)"></span></el-col>
				</el-row>-->
				<!---->
				<!--<el-row>
					<el-col :span="6"><span></span></el-col>
					<el-col :span="12" v-if="loginPassword"><span></span><i class="el-icon-circle-check"></i></el-col>
					<el-col :span="12" v-else><span></span></el-col>

					<el-col :span="6" v-if="loginPassword"><span class="col-change" @click="toChangeLogin(1)"></span></el-col>
					<el-col :span="6" v-else><span class="col-change" @click="toChangeLogin(2)"></span></el-col>
				</el-row>-->

			</div>
		</div>
		<!---->
		<div v-if="settingShow==3">
			<div v-if="iphoneData.phoneStatus==1">
				<h4>-</h4>
				<el-row>
					<el-col :span="24" class="col-head">
						<p style="font-size: 14px;">() </p>

						<div class="demo-input-size">
							<el-input v-model="iphone" :disabled="true" class="iphone-input iphone-input-disabled">
								<i slot="prefix" class="el-input__icon el-icon-mobile-phone"></i>
							</el-input><br />
							<el-input placeholder="" v-model="iphoneData.yanzheng" class="iphone-input">
								<i slot="prefix" class="el-input__icon el-icon-edit-outline"></i>
								<el-button slot="append" v-if="iphoneData.toGetCodeStatus==0" @click="toGetCode(1)"></el-button>
								<el-button slot="append" v-if="iphoneData.toGetCodeStatus==1" disabled>(<span>{{iphoneData.codeTime}}</span>)</el-button>
							</el-input>

							<div style="margin-top:40px;">
								<el-button @click="returnPage"></el-button>
								<el-button type="primary" @click="toChangePhone(3)"></el-button>
							</div>
						</div>
					</el-col>
				</el-row>
			</div>
			<div v-if="iphoneData.phoneStatus==0">
				<h4>-</h4>
				<el-row>
					<el-col :span="24" class="col-head">
						<p style="font-size: 14px;">()</p>

						<div class="demo-input-size">
							
							<el-input v-model="iphoneData.iphoneNew" placeholder="" class="iphone-input">
								<i slot="prefix" class="el-input__icon el-icon-mobile-phone"></i>
							</el-input>
							<br />
							<el-input placeholder="" v-model="iphoneData.yanzheng" class="iphone-input">
								<i slot="prefix" class="el-input__icon el-icon-edit-outline"></i>
								<el-button slot="append" v-if="iphoneData.toGetCodeStatus2==0" @click="toGetCode(2)"></el-button>
								<el-button slot="append" v-if="iphoneData.toGetCodeStatus2==1" disabled>(<span>{{iphoneData.codeTime}}</span>)</el-button>
							</el-input>

							<div style="margin-top:40px;">
								<el-button @click="returnPage"></el-button>
								<el-button type="primary" @click="toChangePhone(4)"></el-button>
							</div>
						</div>
					</el-col>
				</el-row>
			</div>
		</div>
		<!---->
		<div v-if="settingShow==6">
			<h4 v-if="loginPassword">-</h4>
			<h4 v-else>-</h4>
			<el-row>
				<el-col :span="24" class="col-head">
					<p style="font-size: 14px;">(8)</p>
					<div class="demo-input-size">
						<el-input v-if="loginPassword" v-model="loginSecretData.oldPsw" class="iphone-input" placeholder="" type="password"></el-input>
						<i class="el-icon-circle-close" style="color:#F56C6C;position: absolute;left: 70%;top: 25%;" v-if="loginSecretData.oldPswShow"></i>
						<br />
						<el-input v-model="loginSecretData.newPsw" type="password" class="iphone-input" placeholder=""></el-input>
						<i class="el-icon-circle-close" style="color:#F56C6C;position: absolute;left: 70%;top: 43%;" v-if="loginSecretData.newPswShow"></i>
						<br />
						<el-input v-model="loginSecretData.confiPsw" type="password" class="iphone-input" placeholder=""></el-input>
						<i class="el-icon-circle-close" style="color:#F56C6C;position: absolute;left: 70%;top: 61%;" v-if="loginSecretData.confiPswShow"></i>
						<br />
						
						<div style="margin-top:40px;">
							<el-button @click="returnPage"></el-button>
							<el-button type="primary" @click="toChangeLogin(3)"></el-button>
						</div>
					</div>
				</el-col>
			</el-row>
		</div>
		<!---->
		<div v-if="settingShow==2">
			<div v-if="identifiData.status==1">
				<h4>-</h4>
				<el-row>
					<el-col :span="4" class="col-head" style="font-size: 14px;color: #82868A;""></el-col>
					<el-col :span="8" class="col-head" style="font-size: 14px;">{{identifiName}}</el-col>
				</el-row>
				<el-row>
					<el-col :span="4" class="col-head" style="font-size: 14px;color: #82868A;""></el-col>
					<el-col :span="8" class="col-head" style="font-size: 14px;">{{identifiCard}}</el-col>
				</el-row>
				<el-row>
					<el-col :span="4" class="col-head" style="font-size: 14px;color: #82868A;"></el-col>
					<el-col :span="8" class="col-head" style="font-size: 14px;">{{identifiTime}}</el-col>
				</el-row>
				<div style="margin-top:40px;">
					<el-button @click="returnPage"></el-button>
				</div>
			</div>
			<div v-if="identifiData.status==2">
				<h4>-</h4>
				<div class="demo-input-size">
					<el-row>
						<el-col :span="4" class="col-head"></el-col>
						<el-col :span="8" class="col-head">
							<el-input v-model="identifiData.name"></el-input>
						</el-col>
					</el-row>
					<el-row>
						<el-col :span="4" class="col-head"></el-col>
						<el-col :span="8" class="col-head">
							<el-input v-model="identifiData.IDCard"></el-input>
						</el-col>
					</el-row>
					<div style="margin-top:40px;">
						<el-button @click="returnPage"></el-button>
						<el-button type="primary" @click="quitIdentification(3)"></el-button>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import jpg1 from '@/assets/images/1.jpg';
	import {CheckPassWord} from '@/assets/js/public.js' //
	export default {
		data() {
			return {
				settingShow: 1, // 1234567
				headImg: jpg1, //
				name: "herong", //
				identificationStatus: false, //
				identifiName:'',  //
				identifiCard:'430*************99',  //
				identifiTime:'2018-11-07 17:30:32', //
				iphone: 1871234123,
				weixin: 'sweet',
				loginPassword: true, //
				iphoneData: {
					'iphonePre':1871234123, //
					'phoneStatus': 1, //10
					'yanzheng': '', //
					'codeTime': 60, //
					'toGetCodeStatus': 0, //1  
					'toGetCodeStatus2': 0, //1  
					'iphoneNew':''  //
				},
				loginSecretData:{
					oldPsw:'',  //
					oldPswShow:false,
					newPsw:'',  //
					newPswShow:false,
					confiPsw:'', //
					confiPswShow:false,
				},
				identifiData:{
					status:1, //12
					name:'',
					IDCard:''
				}
			}
		},
		methods: {
			//
			toChangeHead: function() {

			},
			// 12
			toChangeName: function(index) {
				let that = this;
				if(index == 1) {
					let htmlContent = `<input class=change-input id=name type=text name=name value=${that.name}>`;
					that.$confirm(htmlContent, '', {
						confirmButtonText: '',
						cancelButtonText: '',
						center: true,
						dangerouslyUseHTMLString: true,
						beforeClose: (action, instance, done) => {
							if(action === 'confirm') {

								if(document.getElementById('name').value != '') {
									that.name = document.getElementById('name').value;
									done();
									that.$message({
										message: '',
										type: 'success'
									});
								} else {
									that.$message('');
								}

							} else {
								done();
							}
						}
					}).then(({
						value
					}) => {}).catch(() => {});

				} else {
					let htmlContent = `<input class=change-input id=name type=text name=name >`;
					that.$confirm(htmlContent, '', {
						confirmButtonText: '',
						cancelButtonText: '',
						center: true,
						dangerouslyUseHTMLString: true,
						beforeClose: (action, instance, done) => {
							if(action === 'confirm') {

								if(document.getElementById('name').value != '') {
									that.name = document.getElementById('name').value;
									done();
									that.$message({
										message: '',
										type: 'success'
									});
								} else {
									that.$message('');
								}

							} else {
								done();
							}
						}
					}).then(({
						value
					}) => {}).catch(() => {});
				}

			},
			//
			quitIdentification: function(index) {
				let that = this;
				if(index == 1) {
					//
					that.settingShow = 2;
				}else if(index == 2){
					//
					that.settingShow = 2;
					that.identifiData.status = 2
				}else if(index == 3){
					//
					
				}
			},
			//
			toChangePhone: function(index) {
				let that = this;
				if(index == 1) {
					//
					that.settingShow = 3;
				} else if(index == 2) {
					//
					that.settingShow = 3;
				} else if(index == 3) {
					// 
					this.$message({
						showClose: true,
						message: '',
						type: 'success'
					});
					//
					that.iphoneData.yanzheng = '';
					that.iphoneData.phoneStatus = 0;
				}else if(index==4){
					//
					that.iphone = that.iphoneData.iphoneNew;
					//
					//  
					if(that.iphoneData.iphonePre){
						//
						this.$message({
							showClose: true,
							message: '',
							type: 'success'
						});
					}else{
						//
						this.$message({
							showClose: true,
							message: '',
							type: 'success'
						});
					}
					that.returnPage();
				}
			},
			//
			toChangeWeixin: function() {

			},
			//
			toChangeLogin: function(index) {
				let that = this;
				if(index==1){
					//
					that.settingShow = 6;
					
				}else if(index==2){
					//
					that.settingShow = 6;
				}else if(index==3){
					//
					if(that.loginPassword){
						if(that.loginSecretData.oldPsw){
							//
							that.loginSecretData.oldPswShow = false;
							if(CheckPassWord(that.loginSecretData.newPsw)){
								//
								that.loginSecretData.newPswShow = false;
								if(that.loginSecretData.newPsw == that.loginSecretData.confiPsw){
									that.loginSecretData.confiPswShow = false;
									 this.$message({
							          	message: '',
							          	type: 'success'
							        });
							        that.loginSecretData.oldPsw='' ; //
									that.loginSecretData.newPsw='' ; //
									that.loginSecretData.confiPsw=''; //
									that.loginPassword = true;
									that.returnPage();
								}else{
									that.$message.error('');
									that.loginSecretData.confiPswShow = true;
								}
							}else{
								that.$message.error('8');
								that.loginSecretData.newPswShow = true;
							}
						}else{
							that.$message.error('');
							that.loginSecretData.oldPswShow = true;
						}
					}else{
						if(CheckPassWord(that.loginSecretData.newPsw)){
							//
							that.loginSecretData.newPswShow = false;
							if(that.loginSecretData.newPsw == that.loginSecretData.confiPsw){
								that.loginSecretData.confiPswShow = false;
								that.$message({
						          	message: '',
						          	type: 'success'
						        });
						        that.loginSecretData.oldPsw='' ; //
								that.loginSecretData.newPsw='' ; //
								that.loginSecretData.confiPsw=''; //
								that.loginPassword = true;
								that.returnPage();
							}else{
								that.$message.error('');
								that.loginSecretData.confiPswShow = true;
							}
						}else{
							that.$message.error('8');
							that.loginSecretData.newPswShow = true;
						}
					}
					
				}
			},
			//
			toGetCode: function(index) {
				//index==1index==2
				//codeTime:60,  //
				//toGetCodeStatus:0,  //1 
				let that = this;
				if(index==1){
					//
					that.iphoneData.toGetCodeStatus = 1;
					//1 1000 
					let t1 = window.setInterval(refreshCount(t1), 1000);
				}else{
					that.iphoneData.toGetCodeStatus2 = 1;
					//1 1000 
					let t2 = window.setInterval(refreshCount(t2), 1000);
				}
				function refreshCount(timeId) {
					that.iphoneData.codeTime--;
					if(that.iphoneData.codeTime <= 0) {
						//  
						that.iphoneData.toGetCodeStatus = 0;
						that.iphoneData.codeTime = 60;
						window.clearInterval(timeId);
					}
				}

			},
			//
			returnPage: function() {
				this.settingShow = 1;
			},
			
		},
		
	}
</script>

<style>
	@import url("../../assets/css/user");
	.el-input.is-disabled .el-input__inner {
		color: #303133;
	}
</style>


