<template>
	<div class="inform">
		<div v-if="listShow==1">
			<h4>  (<span>{{infoMount}}</span>)</h4>
			<!--<el-badge :value="infoMount" class="item"></el-badge>-->
			<el-table :data="tableData" style="width: 100%">
				<el-table-column type="index" width="60" label=""></el-table-column>
				<el-table-column label="" width="300">
					<template slot-scope="scope">
						<span style="overflow: hidden;text-overflow:ellipsis;white-space: nowrap;">{{ scope.row.address }}</span>
					</template>
				</el-table-column>
				<el-table-column label="" width="200">
					<template slot-scope="scope">
						<el-tag type="info" size="medium" v-if="scope.row.status==1"></el-tag>
						<el-tag type="danger" size="medium" v-if="scope.row.status==2"></el-tag>
					</template>
				</el-table-column>
				<el-table-column label="" width="150">
					<template slot-scope="scope">
						<span>{{ scope.row.date }}</span>
					</template>
				</el-table-column>
				<el-table-column label="">
					<template slot-scope="scope">
						<el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
						<el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
					</template>
				</el-table-column>
			</el-table>
			<div class="pages">
				<el-pagination
				  background
				  layout="prev, pager, next"
				  :total="1000">
				</el-pagination>
			</div>
		</div>	
		<div v-if="listShow==2">
			<h4><span>{{detail.name}}</span></h4>
			<el-row >
			  	<el-col :span="24" style="background-color: #ecf3ff;padding: 20px;">
			  		<div v-html="detail.content"></div>
			  	</el-col>
			</el-row>
			<el-row style="text-align: center;">
			  	<el-col :span="24">
			  		<el-button @click="returnPage"></el-button>
			  	</el-col>
			</el-row>
		</div>	
	</div>
</template>

<script>
	import {contentTest} from '@/assets/js/public.js' //
	export default {
		data() {
			return {
				listShow:1,  //12
				infoMount: 12,
				tableData: [{date:'2016-05-02',name:'',status:1,address:' 1518  1518  1518  1518 '},{date:'2016-05-04',name:'',status:1,address:' 1517 '},{date:'2016-05-01',name:'',status:2,address:' 1519 '},{date:'2016-05-03',name:'',status:1,address:' 1516 '},{date:'2016-05-04',name:'',status:2,address:' 1517 '},{date:'2016-05-01',name:'',status:2,address:' 1519 '},{date:'2016-05-03',name:'',status:2,address:' 1516 '}],
				detail:{
					name:'',
					content:contentTest,
				}
			}
		},
		methods: {
			handleEdit(index, row) {
				console.log(index, row);
				let that = this;
				that.listShow = 2;
				that.detail.name = ''; 
			},
			handleDelete(index, row) {
				console.log(index, row);
			},
			returnPage:function(){
				this.listShow = 1;
			}
		}
	}
</script>

<style>
	@import url("../../assets/css/user");
</style>


